<template>
  <div class="stack-small" v-if="!isEditing">
    <el-card 
      class="task-card" 
      :class="{ 'is-done': isDone }"
      shadow="hover">
      <div class="task-content">
        <div class="task-left">
          <el-checkbox
            :id="id"
            v-model="localDone"
            @change="$emit('checkbox-changed')"
            size="large">
            <span class="checkbox-label" :class="{ 'done-text': isDone }">{{label}}</span>
          </el-checkbox>
        </div>
        
        <div class="task-actions">
          <el-tooltip
            content="编辑任务"
            placement="top"
            effect="dark">
            <el-button 
              type="primary" 
              :icon="Edit"
              circle
              class="action-button edit-button" 
              ref="editButton" 
              @click="toggleToItemEditForm">
            </el-button>
          </el-tooltip>
          
          <el-tooltip
            content="删除任务"
            placement="top"
            effect="dark">
            <el-button 
              type="danger" 
              :icon="Delete"
              circle
              class="action-button delete-button" 
              @click="deleteToDo">
            </el-button>
          </el-tooltip>
        </div>
      </div>

      <div class="task-meta" v-if="isDone">
        <el-tag type="success" size="small" effect="light">已完成</el-tag>
      </div>
    </el-card>
  </div>
  <to-do-item-edit-form 
    v-else 
    :id="id" 
    :label="label" 
    @item-edited="itemEdited" 
    @edit-cancelled="editCancelled">
  </to-do-item-edit-form>
</template>

<script>
import ToDoItemEditForm from "./ToDoItemEditForm";
import { Edit, Delete } from '@element-plus/icons-vue';
import { ElMessage, ElMessageBox } from 'element-plus';

export default {
  components: {
    ToDoItemEditForm,
  },
  props: {
    label: { required: true, type: String },
    done: { default: false, type: Boolean },
    id: { required: true, type: String }
  },
  data() {
    return {
      isEditing: false,
      localDone: this.done,
      Edit,
      Delete
    };
  },
  watch: {
    done(newVal) {
      this.localDone = newVal;
    }
  },
  computed: {
    isDone() {
      return this.done;
    }
  },
  methods: {
    deleteToDo() {
      ElMessageBox.confirm(
        'Are you sure you want to delete this task?',
        'Warning',
        {
          confirmButtonText: 'Delete',
          cancelButtonText: 'Cancel',
          type: 'warning',
          draggable: true,
          customClass: 'custom-message-box'
        }
      )
      .then(() => {
        this.$emit('item-deleted');
        ElMessage({
          type: 'success',
          message: 'Task deleted successfully',
          customClass: 'custom-message'
        });
      })
      .catch(() => {
        ElMessage({
          type: 'info',
          message: 'Delete canceled',
          customClass: 'custom-message'
        });
      });
    },
    toggleToItemEditForm() {
      this.isEditing = true;
    },
    itemEdited(newItemName) {
      this.$emit("item-edited", newItemName);
      this.isEditing = false;
      this.focusOnEditButton();
    },
    editCancelled() {
      this.isEditing = false;
      this.focusOnEditButton();
    },
    focusOnEditButton() {
      this.$nextTick(() => {
        const editButtonRef = this.$refs.editButton;
        if (editButtonRef && editButtonRef.$el) {
          editButtonRef.$el.focus();
        }
      });
    }
  }
};
</script>

<style scoped>
.task-card {
  margin-bottom: 16px;
  border-radius: 12px;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid #ebeef5;
}

.task-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 16px rgba(0,0,0,0.08);
}

.task-card.is-done {
  background-color: #f9fafb;
  border-color: #e5e7eb;
}

.task-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
}

.task-left {
  flex-grow: 1;
  margin-right: 16px;
}

.task-actions {
  display: flex;
  gap: 8px;
}

.action-button {
  transition: all 0.2s ease;
}

.action-button:hover {
  transform: scale(1.05);
}

.edit-button {
  background-color: #626aef;
}

.edit-button:hover {
  background-color: #4318FF;
}

.delete-button:hover {
  background-color: #d03050;
}

.checkbox-label {
  font-size: 16px;
  color: #1a1f36;
  margin-left: 8px;
  transition: all 0.3s ease;
  font-weight: 500;
}

.done-text {
  text-decoration: line-through;
  color: #8e8ea0;
}

.task-meta {
  margin-top: 8px;
  padding-top: 8px;
  border-top: 1px solid #ebeef5;
}

:deep(.el-checkbox__input.is-checked + .el-checkbox__label) {
  color: #626aef;
}

:deep(.el-card__body) {
  padding: 16px;
}

:deep(.el-tag) {
  border-radius: 6px;
}

/* 自定义消息框样式 */
:deep(.custom-message-box) {
  border-radius: 12px;
  box-shadow: 0 12px 32px rgba(0,0,0,0.1);
}

:deep(.custom-message) {
  min-width: 300px;
  padding: 12px 16px;
  border-radius: 8px;
}

/* 动画效果 */
.task-card {
  animation: slideIn 0.3s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
</style>